/*------------------------------------*\
  #CHECKOUT PROGRESS BAR
\*------------------------------------*/
// Sticky progress bar, page with admin bar
body:not( .has-checkout-header ).admin-bar .fc-progress-bar[data-sticky-states].is-sticky--top .fc-progress-bar__inner {
	transform: translateY( $global-spacing-tiny );

	@media screen and (min-width: 783px) { // Uses absolute values for breakpoint to match WordPress admin bar
		transform: translateY( $global-spacing-tiny );
	}
}
